import * as React from 'react';
import classNames from 'classnames';
import { Container, Row, Column } from '../grid/Grid';
import styles from './FixedBarBottom.module.css';

type Props = {
  leftColumn?: React.ReactNode;
  rightColumn?: React.ReactNode;
} & React.HTMLAttributes<HTMLElement>;

const FixedBarBottom: React.FunctionComponent<Props> = ({
  className,
  leftColumn,
  rightColumn,
  ...props
}) => {
  return (
    <div className={classNames(styles.container, className)} {...props}>
      <Container>
        <Row className={styles.row}>
          <Column xs={3} md={3} className={styles.columnLeft}>
            {leftColumn}
          </Column>
          {rightColumn && (
            <Column xs={9} md={9} className={styles.columnRight}>
              {rightColumn}
            </Column>
          )}
        </Row>
      </Container>
    </div>
  );
};

export default FixedBarBottom;
