import React, { useState, useEffect } from 'react';
import { graphql, Link } from 'gatsby';
import CallToActionButton from '../callToAction/CallToActionButton';
import Routes from '../../routing/Routes';
import { RootState } from '../../../root.reducer';
import { getPrivacyPolicyStatus, getCurrentUser } from '../../../domain/user/forces/selectors';
import { setPrivacyPolicyStatus } from '../../../domain/user/forces/actions';
import { Dispatch, bindActionCreators } from 'redux';
import { windowExist } from '../../../utils/general.utils';
import { connect } from 'react-redux';
import styles from './PrivacyPolicy.module.css';

type OwnProps = { privacyPolicyStatus?: boolean | null };
type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type Props = OwnProps & StateProps & DispatchProps;

const PrivacyPolicy: React.FC<Props> = (props: Props) => {
  const [privacyPolicyConsent, setPrivacyPolicyConsent] = useState();
  const renderPrivacyPolicyConsentIfNeeded = () => {
    if (props.privacyPolicyStatus || privacyPolicyConsent) {
      return;
    }
    return (
      <div className={styles.container}>
        <div className={styles.textContainer}>
          <span className={styles.text}>
            Denne nettsiden benytter cookies (informasjonskapsler) for å optimalisere opplevelsen
            din.{' '}
            <Link to={Routes.PrivacyPolicyDetail()} className={styles.text}>
              Les mer her
            </Link>
          </span>
        </div>
        <CallToActionButton
          className={styles.button}
          styleType="secondary"
          onClick={() => savePrivacyPolicyStatus()}>
          Den er grei!
        </CallToActionButton>
      </div>
    );
  };

  const savePrivacyPolicyStatus = async () => {
    if (props.user) {
      await props.setPrivacyPolicyStatus(true);
    }
    setPrivacyPolicyConsent(true);
    // for when user is not logged in or guest users.
    if (windowExist()) {
      localStorage.setItem('privacy_policy_status', 'true');
    }
  };

  return <>{renderPrivacyPolicyConsentIfNeeded()}</>;
};

const mapStateToProps = (state: RootState, ownProps: OwnProps) => ({
  privacyPolicyStatus: getPrivacyPolicyStatus(state),
  user: getCurrentUser(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setPrivacyPolicyStatus,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(PrivacyPolicy);
