import * as React from 'react';
import { Squeeze } from 'react-burgers';

type Props = {
  active?: boolean;
  color?: string;
  lineHeight?: number;
} & React.HTMLAttributes<HTMLElement>;

const HamburgerMenu: React.FunctionComponent<Props> = ({
  color = '#fff',
  active = false,
  lineHeight = 4,
  ...props
}) => {
  return <Squeeze lineHeight={lineHeight} padding={0} color={color} active={active} {...props} />;
};

export default HamburgerMenu;
