import * as React from 'react';
import classNames from 'classnames';
import HamburgerMenu from '../hamburgerMenu/HamburgerMenu';
import Routes from '../../routing/Routes';
import { H2 } from '../texts';
import Link from '../link/Link';
import { navigate } from '@reach/router';
import Scroll from 'react-scroll';
import styles from './MobileMenu.module.css';

type Props = {
  isLoggedIn: boolean;
} & React.HTMLAttributes<HTMLElement>;
interface State {
  isOpen: boolean;
}

const menuItems = (isLoggedIn: boolean) => {
  const items = [
    {
      title: 'Hjem',
      link: Routes.Main(),
      type: 'link',
    },
    {
      title: 'Behandlinger',
      link: Routes.TreatmentSection(),
      type: 'pageSection',
    },
    {
      title: 'Gavekort',
      link: Routes.GiftCard(),
      type: 'link',
    },
    {
      title: 'Nettbutikk',
      link: Routes.Store(),
      type: 'link',
    },
    {
      title: 'Om oss',
      link: Routes.AboutSection(),
      type: 'pageSection',
    },
  ];
  if (isLoggedIn) {
    return [
      ...items,
      {
        title: `Min olio`,
        link: Routes.Dashboard(),
        type: 'link',
      },
    ];
  }
  return [
    ...items,
    {
      title: 'Bli medlem',
      link: Routes.Register(),
      type: 'link',
    },
    {
      title: 'Logg inn',
      link: Routes.LogIn(),
      type: 'link',
    },
  ];
};

class MobileMenu extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isOpen: false,
    };
  }

  toggleMenu(open: boolean) {
    this.setState({ isOpen: open });
  }

  async scrollTo(sectionName: string) {
    this.toggleMenu(false);
    const isNotFrontpage = window.location.pathname !== '/';
    if (isNotFrontpage) {
      await navigate('/');
    }
    Scroll.scroller.scrollTo(sectionName, {
      smooth: true,
    });
  }

  render() {
    const { className, isLoggedIn, ...props } = this.props;
    return (
      <div className={className}>
        <div
          className={classNames(styles.container, { [styles.containerVisible]: this.state.isOpen })}
          {...props}>
          <div
            className={classNames(styles.innerContainer, {
              [styles.innerContainerVisible]: this.state.isOpen,
            })}>
            <ul className={styles.list}>
              {menuItems(isLoggedIn).map(menuItem => (
                <li className={styles.listItem} key={menuItem.title}>
                  {menuItem.type === 'link' ? (
                    <Link
                      className={styles.link}
                      to={menuItem.link}
                      onClick={() => this.toggleMenu(false)}>
                      <H2>{menuItem.title}</H2>
                    </Link>
                  ) : (
                    <H2 onClick={() => this.scrollTo(menuItem.link)}>{menuItem.title}</H2>
                  )}
                </li>
              ))}
            </ul>
          </div>
        </div>
        <div className={styles.hamburgerMenu}>
          <HamburgerMenu
            active={this.state.isOpen}
            onClick={() => this.toggleMenu(!this.state.isOpen)}
          />
        </div>
      </div>
    );
  }
}

export default MobileMenu;
