import * as React from 'react';
import { connect } from 'react-redux';
import Routes from '../../routing/Routes';
import FixedBarBottom from '../fixedBarBottom/FixedBarBottom';
import CallToActionLink from '../callToAction/CallToActionLink';
import Link from '../link/Link';
import Logo from '../logo/Logo';
import {
  isLoggedIn,
  getCurrentUser,
  userdataIsLoading,
} from '../../../domain/user/forces/selectors';
import { getFirstName } from '../../../utils/formatting.utils';
import MobileMenu from '../mobileMenu/MobileMenu';
import { RootState } from '../../../root.reducer';
import Scroll from 'react-scroll';
import { navigate } from '@reach/router';
import styles from './BookMenu.module.css';
import { windowExist } from '../../../utils/general.utils';

interface OwnProps {}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

const scrollTo = async (sectionName: string) => {
  if (windowExist()) {
    const isStoreRoute = sectionName === '/store';
    if (isStoreRoute) {
      // Close snipcart store first if opened
      let snipcartBackButton = document.getElementsByClassName('snipcart-modal__close-title');
      if (snipcartBackButton.length > 0) {
        const backToStoreButton = snipcartBackButton[0] as HTMLElement;
        backToStoreButton.click();
      }
      navigate(sectionName);
    } else {
      // To be able to scroll different section on home page, if not currently on home page, navigate to it first and then scroll
      const isNotOnHomePage = window.location.pathname !== '/';
      if (isNotOnHomePage) {
        await navigate('/');
      }
      Scroll.scroller.scrollTo(sectionName, {
        smooth: true,
      });
    }
  }
};

const LoggedOut = (props: Props) => (
  <div className={styles.loggedOutContainer}>
    <span onClick={() => scrollTo(Routes.Store())} className={styles.routeLink}>
      Nettbutikk
    </span>
    <span onClick={() => scrollTo(Routes.TreatmentSection())} className={styles.routeLink}>
      Behandlinger
    </span>
    <Link underlined type="secondary" to={Routes.GiftCard()} className={styles.routeLink}>
      Gavekort
    </Link>
    <span onClick={() => scrollTo(Routes.AboutSection())} className={styles.routeLink}>
      Om oss
    </span>
    <Link underlined type="secondary" to={Routes.LogIn()} className={styles.routeLink}>
      Logg inn
    </Link>
    <CallToActionLink styleType="outlined" to={Routes.Register()} className={styles.buttons}>
      Bli medlem!
    </CallToActionLink>
    <CallToActionLink styleType="secondary" to={Routes.Booking()} className={styles.userSelect}>
      Book time
    </CallToActionLink>
  </div>
);

const LoggedIn = (props: Props) => {
  if (props.isUserDataLoading || props.currentUser == null) {
    return null;
  }
  return (
    <div className={styles.loggedOutContainer}>
      <span onClick={() => scrollTo(Routes.Store())} className={styles.routeLink}>
        Nettbutikk
      </span>
      <span onClick={() => scrollTo(Routes.TreatmentSection())} className={styles.routeLink}>
        Behandlinger
      </span>
      <span onClick={() => scrollTo(Routes.AboutSection())} className={styles.routeLink}>
        Om oss
      </span>
      <Link underlined type="secondary" to={Routes.GiftCard()} className={styles.routeLink}>
        Gavekort
      </Link>
      <CallToActionLink styleType="outlined" to={Routes.Dashboard()} className={styles.buttons}>
        Hei, {getFirstName(props.currentUser.name)}! Gå til Min olio
      </CallToActionLink>
      <CallToActionLink styleType="secondary" to={Routes.Booking()}>
        Book time
      </CallToActionLink>
    </div>
  );
};

const BookMenu: React.FunctionComponent<Props> = props => {
  return (
    <FixedBarBottom
      leftColumn={
        <>
          <div>
            <Logo color="#fff" />
          </div>
        </>
      }
      rightColumn={
        <>
          <div className={styles.rightColumn}>
            {props.isLoggedIn ? <LoggedIn {...props} /> : <LoggedOut {...props} />}
          </div>
          <div className={styles.mobileMenu}>
            <div className={styles.bookingButton}>
              <CallToActionLink styleType="secondary" to={Routes.Booking()}>
                Book time
              </CallToActionLink>
            </div>
            <MobileMenu isLoggedIn={props.isLoggedIn} />
          </div>
        </>
      }
    />
  );
};

const mapStateToProps = (state: RootState) => ({
  isLoggedIn: isLoggedIn(state),
  currentUser: getCurrentUser(state),
  isUserDataLoading: userdataIsLoading(state),
  state,
});

export default connect<StateProps, {}, OwnProps, RootState>(
  mapStateToProps,
  {}
)(BookMenu);
